import React from 'react'
import './Main.css'
import BlueBlockEthereum from '../lib/web3'
import Addresses from '../config/addresses'
import {withAlert} from 'react-alert'
var FontAwesome = require('react-fontawesome')

class Admin extends React.Component {
  constructor (props) {
    super(props)
    this.sendToken = this.sendToken.bind(this)
    this.createSalePool = this.createSalePool.bind(this)
    this.withdrawFromAllPresales = this.withdrawFromAllPresales.bind(this)
    this.state = {
      sendToAddress: '',
      nameValue: '',
      dateValue: '',
      adminAddress: Addresses.adminAddress,
      availableToWithdraw: ''
    }
    this.getEthAmountForAllSales = this.getEthAmountForAllSales.bind(this)
    this.getEthAmountForAllSales()
  }

  async getEthAmountForAllSales () {
    let ethAmount = await BlueBlockEthereum.getEthAcrossAllSales()
    this.setState({ availableToWithdraw: ethAmount })
  }

  async sendToken (toAddress) {
    let txHash = await BlueBlockEthereum.sendToken(toAddress)
    this.showTxSubmittedAlert(txHash)
  }

  async revokeToken (fromAddress) {
    let txHash = await BlueBlockEthereum.revokeToken(fromAddress)
    this.showTxSubmittedAlert(txHash)
  }

  async createSalePool () {
    let name = this.state.nameValue
    if (!name) return
    let date = this.state.dateValue
    if (date) { date = Math.floor(Date.parse(date) / 1000) + 86400 }
    let txHash = await BlueBlockEthereum.createPresale(name, date)
    this.showTxSubmittedAlert(txHash)
  }

  async withdrawFromAllPresales () {
    let presalesWithBalance = await BlueBlockEthereum.getPresalesWithBalance()
    await BlueBlockEthereum.releaseFromPresales(presalesWithBalance)
    await BlueBlockEthereum.releaseFromFactory()
  }

  showTxSubmittedAlert (txHash) {
    this.props.alert.show(<p>
      <a href={'https://etherscan.io/tx/' + txHash}>Check TX Status</a>
    </p>)
  }

  render () {
    return (<div className="Admin" id="flexcol">
      <div className="uiItem">
        <h3>ADMIN Dashboard</h3>
      </div>
      <hr/>
      <div className="uiItem">
        <a className="tooltip">To create a new pre-sale
          <b> SMART CONTRACT </b>
        input the pre-sale name and the final day that contributions will be accepted.</a>
      </div>
      <div className="uiItem" id="flexrow">
        <input className="adminInput adminContractName" type="text" placeholder="Name a new BLBL pre-sale." value={this.state.nameValue} onChange={event => this.setState({nameValue: event.target.value})}/>
        <input className="adminInput adminDate" type="date" placeholder="Closing Date" value={this.state.dateValue} onChange={event => {
          this.setState({dateValue: event.target.value})
        }}/>
        <button onClick={this.createSalePool}>
          <FontAwesome name='file-o' size="2x" id="icon"/>
        </button>
      </div>
      <hr/>
      <div className="uiItem">
        <a className="tooltip">To allocate or revoke a
          <b> BLBL </b>
        token, input the appropriate address and use the + or - buttons.</a>
      </div>
      <div className="uiItem" id="flexrow">
        <input className="adminInput" placeholder="Add or Remove BLBL token from this address." value={this.state.sendToAddress} onChange={event => this.setState({sendToAddress: event.target.value})}/>
        <button onClick={() => this.sendToken(this.state.sendToAddress)}>
          <FontAwesome name='plus-square-o' size="2x" id="icon"/>
        </button>
        <button onClick={() => this.revokeToken(this.state.sendToAddress)}>
          <FontAwesome name='minus-square-o' size="2x" id="icon"/>
        </button>
      </div>
      <hr/>
      <div className="uiItem">
        <a className="tooltip">Total amount available for withdrawal to <b>{ this.state.adminAddress }</b>.</a>
      </div>
      <div className="uiItem" id="flexrow">
        <input className="adminInput" value={this.state.availableToWithdraw} type="number" readOnly="readOnly"/>
        <button onClick={() => { this.withdrawFromAllPresales() }}>
          <FontAwesome name='send' id="icon"/>
        </button>
      </div>
    </div>)
  }
}

export default withAlert(Admin)

