import React from 'react'
import './Main.css'
import PresaleList from './PresaleList'
import Description from './Description'
import Header from './Header'
import Admin from './Admin'
import BlueBlockEthereum from '../lib/web3'
import Footer from './Footer'

class App extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      presales: [],
      hasToken: false,
      isAdmin: false
    }
  }

  async componentDidMount () {
    // @Alex: This is how you would check if the current metamask user is an admin
    let isAdmin = await BlueBlockEthereum.isAdmin()
    let presales = await BlueBlockEthereum.getPresaleList()
    let hasToken = await BlueBlockEthereum.hasBlueBlockToken()
    this.setState({ presales, hasToken, isAdmin })
  }

  render () {
    return (
      <div classNameName="App">
        <Header />
        { this.state.isAdmin && <Admin /> }
        <PresaleList presales={this.state.presales} hasToken={this.state.hasToken} isAdmin={this.state.isAdmin}/>
        <Description />
        <Footer />
        <link rel="stylesheet" href="https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css" />
      </div>
    )
  }
}

export default App



