import React from 'react'
import './Main.css'
import Presale from './Presale'

class PresaleList extends React.Component {
  render () {
    return (
      <div className="PresaleList">
        {
          this.props.presales.map((presale, index) => {
            return <Presale name={presale.name} address={presale.address} id={index} key={index} hasToken={this.props.hasToken}/>
          })
        }
      </div>
    )
  }
}

export default PresaleList



