import React from 'react'
import './Main.css'
import copy from 'copy-to-clipboard'
import FontAwesome from 'react-fontawesome'
import BlueBlockEthereum from '../lib/web3'
import {withAlert} from 'react-alert'

class Presale extends React.Component {
  constructor (props) {
    super(props)
    this.copyClipboard = this.copyClipboard.bind(this)
    this.setEthVal = this.setEthVal.bind(this)
    this.getEthBalance = this.getEthBalance.bind(this)
    this.hideSale = this.hideSale.bind(this)
    this.destroySale = this.destroySale.bind(this)
    this.releaseFunds = this.releaseFunds.bind(this)
    this.getEthBalance(this.props.address)
    this.state = {
      ethAddress: this.props.address,
      ethVal: null,
      ethBalance: 0,
      isAdmin: false
    }
    this.isAdmin = this.isAdmin.bind(this)
    this.isAdmin()
  }

  copyClipboard () {
    copy(this.state.ethAddress)
    this.props.alert.show(<h4>{this.props.name} address copied.</h4>)
  }

  async isAdmin () {
    let isAdmin = await BlueBlockEthereum.isAdmin()
    this.setState({ isAdmin: isAdmin })
  }

  async contributeEth (address, ethVal) {
    await BlueBlockEthereum.contributeToPresale(address, ethVal)
  }

  setEthVal (newVal) {
    this.setState({
      ethVal: newVal.target.value
    })
  }

  async getEthBalance (address) {
    let ethBalance = await BlueBlockEthereum.getEthBalance(address)
    this.setState({ethBalance: ethBalance})
  }

  async hideSale () {
    if (!this.state.ethAddress) return
    let txHash = await BlueBlockEthereum.hidePresale(this.state.ethAddress)
    this.props.alert.show(<p><a href={'https://etherscan.io/tx/' + txHash}>Check Status</a></p>)
  }

  async destroySale () {
    if (!this.state.ethAddress) return
    let txHash = await BlueBlockEthereum.destroyPresale(this.state.ethAddress)
    this.props.alert.show(<p><a href={'https://etherscan.io/tx/' + txHash}>Check Status</a></p>)
  }

  async releaseFunds () {
    if (!this.state.ethAddress) return
    let releaseTxHash = await BlueBlockEthereum.releaseFromPresale(this.state.ethAddress)
    this.props.alert.show(<p><a href={'https://etherscan.io/tx/' + releaseTxHash}>Check Status</a></p>)
    let withdrawTxHash = await BlueBlockEthereum.releaseFromFactory()
    this.props.alert.show(<p><a href={'https://etherscan.io/tx/' + withdrawTxHash}>Check Status</a></p>)
  }

  render () {
    return (<div className="Presale">
      <div className="presale-item" id="flexcol">
        <div className="presale-heading" id="flexrow">
          <h3>{this.props.name}</h3>
        </div>
        <hr/>
        {
          this.state.isAdmin &&
          <div className="presale-admin" id="flexrow">
            <p id="adminText">Balance {this.state.ethBalance} ETH</p>
            <button className="btnAdmin" Click={this.hideSale}>HIDE</button>
            <button className="btnAdmin" onClick={this.destroySale}>DESTROY</button>
            <button className="btnAdmin" onClick={this.releaseFunds}>RELEASE</button>
          </div>
        }
        <div className="presale-content" id="flexrow">
          <textarea readOnly="readOnly" className="address">
            {this.state.ethAddress}
          </textarea>
          <button onClick={this.copyClipboard}>
            <FontAwesome name="clone" />
          </button>
        </div>
        <hr/>
        <div className="uiItem">
        <a className="tooltip">Writing your contribution amount below and pressing send will generate a metamask trasaction.</a>
        </div>
        {
          this.props.hasToken &&
          <div className="uiItem" id="flexrow">
            <a className="tooltip">Contribution amount:</a>
            <input type="number" step="0.01" placeholder="Contribution amount in Ether." min="0.00" value={this.state.ethVal} onChange={this.setEthVal}/>
            <button onClick={() => this.contributeEth(this.state.ethAddress, this.state.ethVal)}><FontAwesome name="send" size="1x"/></button>
          </div>
        }
      </div>
    </div>)
  }
}
export default withAlert(Presale)



