import React from 'react'
import './Main.css'
import BlueBlockEthereum from '../lib/web3'
var FontAwesome = require('react-fontawesome')

class Header extends React.Component {
  constructor () {
    super()
    this.state = {
      hasToken: false,
      accountBalance: 0,
      isAdmin: false,
      hasAccount: false
    }
  }

  async componentDidMount () {
    let ethAddress = await BlueBlockEthereum.getAddress()
    this.setState({hasAccount: ethAddress !== undefined})
    if (!this.state.hasAccount) return
    let hasToken = await BlueBlockEthereum.hasBlueBlockToken()
    this.setState({hasToken: hasToken})
    let accountBalance = await BlueBlockEthereum.getEthBalance()
    this.setState({accountBalance: accountBalance})
    let isAdmin = await BlueBlockEthereum.isAdmin()
    this.setState({isAdmin: isAdmin})
  }

  renderAuthPositive () {
    return (
      <div className={'auth ' + 'authPositive'} id="flexrow">
        <img width='18px' height='18px'
          id="icon"
          src={require('../assets/metamask_logo.png')}/>
        <p>BLBL Token Found | Available ETH: {this.state.accountBalance} | You're ready to contribute to a BlueBlock Pre-sale.</p>
      </div>
    )
  }

  renderAuthNegative () {
    return (
      <div className='auth authNegative' id="flexrow">
        <img width='18px' height='18px'
          id="icon"
          src={require('../assets/metamask_logo.png')}/>
        <p>BLBL Token NOT FOUND in active Metamask wallet. Any Ethereum sent from this wallet will be rejected. <a id="highlight" href="#guide"><strong>Find out more.</strong></a></p>
      </div>
    )
  }

  renderAuthNeutral () {
    return (
      <div className='auth authNeutral' id="flexrow">
        <p>To contribute to a BlueBlock pre-sale you must obtain 1 BLBL token. <a id="highlight" href="#guide"><strong>Find out more.</strong></a></p>
      </div>
    )
  }

  renderAuthAdmin () {
    return (
      <div className='auth authAdmin' id="flexrow">
        <FontAwesome name='rocket' id="icon"/>
        <p>Welcome back James. Ready to go to the moon?</p>
      </div>
    )
  }

  renderAuth () {
    if (this.state.isAdmin) {
      return this.renderAuthAdmin()
    } else if (!this.state.hasAccount) {
      return this.renderAuthNeutral()
    } else if (this.state.hasToken) {
      return this.renderAuthPositive()
    } else {
      return this.renderAuthNegative()
    }
  }

  render () {
    return (<div className="Header">
      {
        this.renderAuth()
      }
      <div className="main">
        <div className="title" id="flexrow">
          <img height="150px" width="150px" alt="BlueBlock Logo" src={require('../assets/blbl_logo')}/>
          <h1 className="blbl">Blue<br/>Block</h1>
        </div>
      </div>
    </div>)
  }
}

export default Header



