import BlueBlockToken from './contracts/BlueBlockToken'
import SalePoolFactory from './contracts/SalePoolFactory'
import SalePool from './contracts/SalePool'

const contracts = {
  BlueBlockToken: {
    abi: BlueBlockToken.abi,
    bytecode: BlueBlockToken.bytecode
  },
  SalePoolFactory: {
    abi: SalePoolFactory.abi,
    bytecode: SalePoolFactory.bytecode
  },
  SalePool: {
    abi: SalePool.abi,
    bytecode: SalePool.bytecode
  }
}

export default contracts



