import Web3 from 'web3'
import Contracts from './blueblockcontracts'
import Addresses from '../config/addresses'
import BigNumber from 'bignumber.js'

const funcs = {

  getWeb3: async () => {
    return new Promise((resolve) => {
      let web3
      if (typeof window.web3 === 'undefined') {
        console.error('No Web3 detected, falling back to default mode')
        web3 = new Web3(Web3.providers.HttpProvider('https://kovan.infura.io'))
      } else {
        web3 = new Web3(window.web3.currentProvider)
        web3.eth.defaultAccount = window.web3.eth.defaultAccount
        resolve(web3)
      }
    })
  },

  getAddress: async (_callback) => {
    let web3 = await funcs.getWeb3()
    return web3.eth.accounts[0]
  },

  hasBlueBlockToken: async (address = '') => {
    let web3 = await funcs.getWeb3()
    if (!address) address = web3.eth.accounts[0]
    if (!address) return false
    let tokenContract = await funcs.getTokenContract()
    let balance = await funcs.getTokenBalance(tokenContract, address)
    return balance.toNumber() >= 1
  },

  getTokenBalance: (tokenContract, address) => {
    return new Promise((resolve) => {
      tokenContract.balanceOf.call(address, (e, balance) => {
        resolve(balance)
      })
    })
  },

  getEthBalance: async (address = '') => {
    let web3 = await funcs.getWeb3()
    if (!address) address = web3.eth.accounts[0]
    if (!address) return
    return new Promise((resolve) => {
      web3.eth.getBalance(address, (e, balance) => {
        resolve(web3.fromWei(balance, 'ether').toNumber())
      })
    })
  },

  isAdmin: async () => {
    let web3 = await funcs.getWeb3()
    if (!web3.eth.accounts[0]) return false
    return web3.eth.accounts[0].toLowerCase() === Addresses.adminAddress.toLowerCase()
  },

  getPresaleList: async () => {
    let web3 = await funcs.getWeb3()
    return new Promise(async (resolve) => {
      let poolFactoryContractMeta = Contracts.SalePoolFactory
      let poolFactoryContract = web3.eth.contract(poolFactoryContractMeta.abi).at(Addresses.poolFactoryAddress)
      poolFactoryContract.presaleCount.call(async (e, numberOfPresales) => {
        let presales = []
        for (let i = 0; i < numberOfPresales; i++) {
          presales.push(await funcs.getPresale(poolFactoryContract, i))
        }
        resolve(presales)
      })
    })
  },

  getPresale: async (poolFactoryContract, i) => {
    let web3 = await funcs.getWeb3()
    return new Promise((resolve) => {
      poolFactoryContract.presales.call(i, (e, address) => {
        // now we have the address, we should get the name
        let salePoolContractMeta = Contracts.SalePool
        let salePoolContract = web3.eth.contract(salePoolContractMeta.abi).at(address)
        salePoolContract.name.call((e, name) => {
          resolve({address, name})
        })
      })
    })
  },

  hidePresale: async (address) => {
    if (!address) return
    let web3 = await funcs.getWeb3()
    return new Promise((resolve) => {
      let poolFactoryContractMeta = Contracts.SalePoolFactory
      let poolFactoryContract = web3.eth.contract(poolFactoryContractMeta.abi).at(Addresses.poolFactoryAddress)
      poolFactoryContract.hideSale(address, (e, txHash) => {
        if (!e) return resolve(txHash)
      })
    })
  },

  showPresale: async (address) => {
    if (!address) return
    let web3 = await funcs.getWeb3()
    return new Promise((resolve) => {
      let poolFactoryContractMeta = Contracts.SalePoolFactory
      let poolFactoryContract = web3.eth.contract(poolFactoryContractMeta.abi).at(Addresses.poolFactoryAddress)
      poolFactoryContract.showSale(address, (e, txHash) => {
        if (!e) return resolve(txHash)
      })
    })
  },

  destroyPresale: async (address) => {
    if (!address) return
    let web3 = await funcs.getWeb3()
    return new Promise((resolve) => {
      let poolFactoryContractMeta = Contracts.SalePoolFactory
      let poolFactoryContract = web3.eth.contract(poolFactoryContractMeta.abi).at(Addresses.poolFactoryAddress)
      poolFactoryContract.destroySale(address, (e, txHash) => {
        if (!e) return resolve(txHash)
      })
    })
  },

  contributeToPresale: async (address, ethVal) => {
    let web3 = await funcs.getWeb3()
    return new Promise((resolve) => {
      web3.eth.sendTransaction({to: address, value: web3.toWei(ethVal, 'ether')}, (e, txHash) => {
        if (!e) return resolve(txHash)
      })
    })
  },

  createPresale: async (name, endTimestamp) => {
    if (!name) return false
    if (!endTimestamp) endTimestamp = 0
    let web3 = await funcs.getWeb3()
    let poolFactoryContractMeta = Contracts.SalePoolFactory
    let poolFactoryContract = web3.eth.contract(poolFactoryContractMeta.abi).at(Addresses.poolFactoryAddress)
    return new Promise((resolve) => {
      poolFactoryContract.createSale(Addresses.tokenAddress, name, endTimestamp, (e, txHash) => {
        if (!e) return resolve(txHash)
      })
    })
  },

  getEthAcrossAllSales: async () => {
    let presales = await funcs.getPresaleList()
    let totalBalance = new BigNumber(0)
    let presaleBalance = new BigNumber(0)
    for (let presale of presales) {
      presaleBalance = await funcs.getEthBalance(presale.address)
      totalBalance = totalBalance.add(presaleBalance)
    }
    return totalBalance.toNumber()
  },

  getPresalesWithBalance: async () => {
    let presales = await funcs.getPresaleList()
    let presaleBalance = new BigNumber(0)
    let presalesWithBalance = []
    for (let presale of presales) {
      presaleBalance = await funcs.getEthBalance(presale.address)
      if (presaleBalance > 0) presalesWithBalance.push(presale.address)
    }
    return presalesWithBalance
  },

  releaseFromPresales: async (presales) => {
    for (let address of presales) {
      await funcs.releaseFromPresale(address)
    }
  },

  releaseFromPresale: async (address) => {
    if (!funcs.isAdmin()) return false
    let web3 = await funcs.getWeb3()
    let poolFactoryContractMeta = Contracts.SalePoolFactory
    let poolFactoryContract = web3.eth.contract(poolFactoryContractMeta.abi).at(Addresses.poolFactoryAddress)
    return new Promise((resolve) => {
      poolFactoryContract.withdrawFromSalePool(address, (e, txHash) => {
        resolve(txHash)
      })
    })
  },

  releaseFromFactory: async () => {
    if (!funcs.isAdmin()) return false
    let web3 = await funcs.getWeb3()
    let poolFactoryContractMeta = Contracts.SalePoolFactory
    let poolFactoryContract = web3.eth.contract(poolFactoryContractMeta.abi).at(Addresses.poolFactoryAddress)
    return new Promise((resolve) => {
      poolFactoryContract.withdraw((e, txHash) => {
        resolve(txHash)
      })
    })
  },

  sendToken: async (address) => {
    if (!address) return false
    if (!funcs.isAdmin()) return false
    let tokenContract = await funcs.getTokenContract()
    return new Promise((resolve) => {
      tokenContract.transfer(address, 1, (e, txHash) => {
        if (!e) return resolve(txHash)
      })
    })
  },

  revokeToken: async (address) => {
    if (!address) return false
    if (!funcs.isAdmin()) return false
    let tokenContract = await funcs.getTokenContract()
    return new Promise((resolve) => {
      tokenContract.revoke(address, (e, txHash) => {
        if (!e) return resolve(txHash)
      })
    })
  },

  getTokenContract: async () => {
    let web3 = await funcs.getWeb3()
    return web3.eth.contract(Contracts.BlueBlockToken.abi).at(Addresses.tokenAddress)
  },

  getTokenHolders: async () => {
    let numberOfHolders = await funcs.getNumberOfTokenHolders()
    let holders = []
    for (let i = 0; i < numberOfHolders; i++) {
      let holderAddress = await funcs.getTokenHolderAddress(i)
      let holderName = await funcs.getTokenHolderName(holderAddress)
      holders.push({address: holderAddress, name: holderName})
    }
    return holders
  },

  getNumberOfTokenHolders: async () => {
    let tokenContract = await funcs.getTokenContract()
    return new Promise((resolve) => {
      tokenContract.numberOfHolders.call((e, holderCount) => {
        resolve(holderCount.toNumber())
      })
    })
  },

  getTokenHolderName: async (address) => {
    let tokenContract = await funcs.getTokenContract()
    return new Promise((resolve) => {
      tokenContract.holders.call(address, (e, name) => {
        resolve(name)
      })
    })
  },

  getTokenHolderAddress: async (index) => {
    let tokenContract = await funcs.getTokenContract()
    return new Promise((resolve) => {
      tokenContract.holderAddresses.call(index, (e, address) => {
        resolve(address)
      })
    })
  }

}

export default funcs



