import React from 'react'
import './Main.css'

class Description extends React.Component {
  render () {
    return (<div className="Description">
      <div id="guide">
        <h2>How to contribute:</h2>
        <p>Contributions are authenticated through the use of the BlueBlock KYC token (BLBL). BlueBlock will not accept any contributions from an Ethereum address that does not hold a BLBL Token and any such transactions will fail.
        BLBL Token holders can contribute to an appropriate listed pre-sale by sending Ether to the provided <i>Smart Contract Address</i>.
        </p><br />
        <p>To send Ethereum to a BLBL Smart Contract we recommend using <a href="https://metamask.io">metamask.io</a> or <a href="https://myetherwallet.com">myetherwallet (mew)</a>. When using Metamask this page will detect the currrent active wallet and advise your current balance and if you're holding the appropriate BLBL token.
        </p>
      </div>
    </div>)
  }
}

export default Description



