import React from 'react'
import './Main.css'
var FontAwesome = require('react-fontawesome');

class Footer extends React.Component {
  render() {
    return (<div className="Footer" id="flexcol">
      <a className="footerText">
        <FontAwesome id="icon" name='paint-brush'/>
        Company Name
        <FontAwesome id="icon" name='globe'/>
        www.website.com
        <FontAwesome id="icon" name='envelope'/>
        email@emailaddress.com
      </a>
    </div>)
  }
}

export default Footer



